input[type=range] {
    -webkit-appearance: none;
    margin: 10px 0;
    width: 80%;
}

input[type=range]:focus {
    outline: none;
}

input[type=range]::-webkit-slider-thumb {
    box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
    border: 0px solid #000000;
    height: 20px;
    width: 39px;
    border-radius: 25px;
    /* for cursor color */
    background: white;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -3.6px;
}



.first-slider::-webkit-slider-runnable-track {
    width: 80%;
    height: 11px;
    cursor: pointer;
    box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
    background: #5BC2E7;
    /* Change this to your desired color */
    border-radius: 25px;
    border: 0px solid #000101;
}

.second-slider::-webkit-slider-runnable-track {
    width: 80%;
    height: 11px;
    cursor: pointer;
    box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
    background: #FFCD00;
    /* Change this to your desired color */
    border-radius: 25px;
    border: 0px solid #000101;
}

.third-slider::-webkit-slider-runnable-track {
    width: 80%;
    height: 11px;
    cursor: pointer;
    box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
    background: #E31C79;
    /* Change this to your desired color */
    border-radius: 25px;
    border: 0px solid #000101;
}


/* Add specific styling for the email input */
input[type="email"] {
    width: 80%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 10px;
    margin: 10px 0;
    font-size: 16px;
}

/* Add some additional styles for the email input on focus */
input[type="email"]:focus {
    outline: none;
    border-color: #5BC2E7;
    /* Change this to your desired color */
    box-shadow: 0 0 5px #5BC2E7;
    /* Change this to your desired color */
}


/* Customize the submit button */
input[type="submit"] {
    background-color: #5BC2E7;
    /* Change this to your desired color */
    color: #fff;
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

/* Add a hover effect to the submit button */
input[type="submit"]:hover {
    background-color: #4DAED4;
    /* Change this to your desired hover color */
}

